import React from 'react'
import { SearchBar } from 'antd-mobile';
import  {Carousel}  from 'antd-mobile';
import  {Flex}  from 'antd-mobile';
import Tab from './Tab'
import './Home.css'
import store from './reduxWork/store'
class Home extends React.Component {
  constructor(){
    super();
    this.state = store.getState();
    this.getList = this.getList.bind(this);
    store.subscribe(this.getList)
  }
  componentDidMount=()=> {
    setTimeout(() => {
      this.setState({
        data: ['gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu', 
           'aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50',
           'aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX',
           'gw.alicdn.com/imgextra/i4/128/O1CN01L9c1rK1CofKGrrN4Z_!!128-0-ppp-picassogw',
           'gw.alicdn.com/imgextra/i4/81/O1CN01LbIJTv1CT8iDY67wG_!!81-0-lubanu',
           'gw.alicdn.com/imgextra/i1/2/O1CN0169xZ5Y1BsxQEkuxfV_!!2-0-lubanu',
           'img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000006860/O1CN018dxPb020XvdsK4Y0n_!!6000000006860-0-octopus',
           'gw.alicdn.com/imgextra/i3/86/O1CN01QaJCKY1CVQgwb9GvC_!!86-0-lubanu'
          ],
      });
    }, 100);
    fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
        .then((res) => res.json())
        .then((myJson) => {
          console.log(myJson);
        
          let values = myJson.data;
          const action = {
              type:'get_datalist',
              list:values
          }
          store.dispatch(action);
          console.log(this.state);
    });
  }
  getList(){
      this.setState(store.getState());
  }
  getInfo(idx) {
    var arr = this.state.datalist;
    for (var i = idx; i < arr.length; i++) {
      let id=store.getState().datalist[i].id
        return (
            <Flex className='flex' onClick={() => this.props.history.push(`/detail1/${id}`)}>
                <Flex.Item>< img style={{ width: '177px', height: '200px', }}  
                    src={arr[i].img} ></img><p style={{color:"red"}}>￥{arr[i].price}</p ><p>{arr[i].title}</p >
                </Flex.Item>
            </Flex>
        )
     }
  }

  render(){
    return (
      <div style={{margin:0}}>
          <SearchBar 
              placeholder="寻找宝贝店铺"
              onFocus={
                  ()=>{this.props.history.push("/home/search")}
              }
              
          />
          <Carousel 
            autoplay
            infinite
          >
            {this.state.data.map((val, index) => (
              <a
                key={val}
                style={{
                  display: 'block',
                  position: 'relative',
                
                }}
              >
              <img
                src={`https://${val}.jpg`}
            
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));

                }}
              />
              </a>
            ))}
          
          </Carousel>
          <div className='little-header'>
              <img src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png" className='little-header-img' />
          </div>
          <div >
              <div >
                  {this.getInfo(0)}
                  {this.getInfo(1)}
                  {this.getInfo(2)}
                  {this.getInfo(3)}
                  {this.getInfo(4)}
                  {this.getInfo(5)}
                  {this.getInfo(6)}
                  {this.getInfo(7)}
                  {this.getInfo(8)}
                  {this.getInfo(9)}
              </div>
             
          </div> 
          <Tab />   
      </div>

    ) 
  } 
}

export default Home
