import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import store from './reduxWork/store';

class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={ 
                            <i style={{fontSize:22}} className='iconfont icon-Homehomepagemenu'></i>
                        }
                        selectedIcon={ 
                            <i style={{fontSize:22}} className='iconfont icon-Homehomepagemenu1'></i>
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang1'></i>
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang'></i>
                        }
                        title="收藏"
                        key="shoppingcar"
                        selected={ pathname === '/shoucang' }
                        onPress={ () => {
                            if(!localStorage.getItem('userinfo')){
                                this.props.history.push({
                                    pathname:'/login',
                                    state1:this.props.location
                                })
                            }else{
                                this.props.history.push('/shoucang')
                            }
                           
                        } }
                        data-seed="logId1"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-dingdanliebiao'></i>
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-dingdanliebiao-tianchong'></i>
                        }
                        title="订单列表"
                        key="goods"
                        selected={ pathname === '/goods' }
                        onPress={ () => {
                            this.props.history.push('/goods')
                        } }
                        data-seed="logId"
                    >
                        
			        </TabBar.Item>
                    <TabBar.Item
                        icon={ 
                            <i style={{fontSize:22}} className='iconfont icon-wode'></i>
                        }
                        selectedIcon={ 
                            <i style={{fontSize:22}} className='iconfont icon-wodedangxuan'></i>
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId"
                    >   
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Tab);