import React from 'react'

const Login = (props) => {
    const login = ()=>{
        localStorage.setItem('userinfo','tom')
        props.history.replace(props.location.state1)
        console.log(props.location.state1)
    }
    return (
        <div>
            <button style={{
                width:'35px',
                height: '35px',
                borderRadius:'10rem',
                border:0,
                position: 'absolute',
                top:'40px',
                left:'10px',
                display:'block',
                background:'rgba(0, 0, 0, 0.4)',
                color: '#fff'}}
                onClick={props.history.goBack}
            > <i className='iconfont icon-shangyiji'  ></i> </button>
            <span style={{
                color:'red',
                position: 'absolute',
                top: '150px',
                left: '160px',
                fontsize: '24px'
            }}>登录</span>
            <input type='text' style={{
                position:'relative',
                top: '180px',
                left: '100px',
                height: '25px'
            }} placeholder='请输入用户名'></input>
            <input type='password' style={{
                position:'absolute',
                top: '215px',
                left: '100px',
                height: '25px'
            }} placeholder='请输入密码'></input>
            
            
            <button onClick={login} style={{
                position:'absolute',
                top: '250px',
                left: '160px',
                height: '25px'
            }}>登录</button>
        </div>
    )
}

export default Login