import { withRouter } from 'react-router-dom';
import React,{useState} from 'react'
import { Button, TabBar } from 'antd-mobile';
import './Mytap.css'
import store from './reduxWork/store'
class Mytap extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          selectedTab: 'redTab',
        };
      }
   
    render() {
        // const [icoStatus,setIcoStatus]=useState(true)
        // const iconSouCangData=(event,props)=>{
        //     setIcoStatus(!icoStatus)
        // }
        const id = this.props.location.pathname.substring(9,12);
        console.log(id);
        let datalist=store.getState().datalist;
        let shoucang=datalist[id-100];
        console.log(shoucang);
 
        return (
            // <div style={ {
            //     position: 'fixed',
            //     width: '50%',
            //     bottom: 0,       
            // } }>
            //    <div className='icon1'>
            //         <i className='iconfont icon-dianpu1'></i>
            //         <span >店铺</span>
            //    </div>
            //    <div className='icon1' >
            //         <i className='iconfont icon-kefu' ></i>
            //         <span className='kefu'>客服</span>
            //    </div>
            //    <div className='icon1'  >
            //        <i className={icoStatus?"iconfont icon-shoucang1":"iconfont icon-shoucang"} onpress={(e)=>iconSouCangData(event,props)}></i>
               
            //         <span className='shoucang'>收藏</span>
            //    </div>
            
             
            // </div>
            <div style={ {
                position: 'fixed',
                width: '36%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="home"
                        icon={ 
                            <i style={{fontSize:22}} className='iconfont icon-dianpu1'></i>
                        }
                        selectedIcon={ 
                            <i style={{fontSize:22}} className='iconfont icon-dianpu'></i>
                        }
                        // selected={ pathname === '/home' }
                        // onPress={ () => {
                        //     this.props.history.push('/home');
                        // } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-kefu'></i>
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-10'></i>
                        }
                        title="客服"
                        key="shoppingcar"
                        // selected={ pathname === '/shoppingcar' }
                        // onPress={ () => {
                        //     this.props.history.push('/shoppingcar')
                        // } }
                        data-seed="logId1"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang1'></i>
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang'></i>
                        }
                        title="收藏"
                        key="goods"
                        selected={this.state.selectedTab === 'yellowTab'}
                        onPress={() => {
                            

                          this.setState({
                            selectedTab: 'yellowTab',
                          });
                          if(!localStorage.getItem('userinfo')){
                            this.props.history.push({
                                pathname:'/login',
                                state1:this.props.location
                            })
                          }else{
                            //  this.props.history.push(`/detail1/${id}`)
                             const action = {
                                type:'get_shoucanglist',
                                list:shoucang
                              }
                              store.dispatch(action);

                          }
                        //   const action = {
                        //     type:'get_shoucanglist',
                        //     list:shoucang
                        //   }
                        //   store.dispatch(action);
                      
                        }}
                        // onClick={()=>{

                           
                        // }}
                        // onClick={()=>{
                    
                        //         store.dispatch({
                        //             type:'ADD_TODO',
                        //             list:store.getState().datalist[id-100]
                        //         })
        
                        // }} 
                       
                        data-seed="logId"
                    >
                        
			        </TabBar.Item>
                    
                   
                </TabBar>
                <div className="icon2">
                    <span className='cart'>加入购物车</span>
               </div>
               <div className="icon3">
                    <span className='buy'>立即购买</span>
               </div>
            </div>
         
            
        );
    }
}
export default withRouter(Mytap);
