// export default App;
import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'

import Home from './taobao/Home'
import Shoucang from './taobao/Shoucang'
import My from './taobao/My'
import Goods from './taobao/Goods'
import Search from './taobao/Search'
import Detail1 from './taobao/Detail1';
import Login from './taobao/Login'


const App = ()=>{
	return <Router>
		<Switch>
			<Route  exact path='/react5/build' component={Home} ></Route>
			<Route  path='/home' component={Home} ></Route>
			<Route  path='/goods' component={Goods} ></Route>
			<Route  path='/home/search' component={Search} />	
			<Route  path='/detail1/:id' component={Detail1} ></Route>
			<Route  path='/login' component={Login} ></Route>

			<Route  path='/shoucang' component={Shoucang}></Route>
			<Route  path='/my' component={My}></Route>
		</Switch>
	</Router>
}
export default App;
