const defaultState = {
    datalist:[{}],
    shoucang:[],
    userinfo:'',
    imgObj:{},
    data: ['gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu', 
    'aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50',
    'aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX',
    'gw.alicdn.com/imgextra/i4/128/O1CN01L9c1rK1CofKGrrN4Z_!!128-0-ppp-picassogw',
    'gw.alicdn.com/imgextra/i4/81/O1CN01LbIJTv1CT8iDY67wG_!!81-0-lubanu',
    'gw.alicdn.com/imgextra/i1/2/O1CN0169xZ5Y1BsxQEkuxfV_!!2-0-lubanu',
    'img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000006860/O1CN018dxPb020XvdsK4Y0n_!!6000000006860-0-octopus',
    'gw.alicdn.com/imgextra/i3/86/O1CN01QaJCKY1CVQgwb9GvC_!!86-0-lubanu'
   ],
};

export default (state = defaultState,action) => {
    if(action.type==='get_datalist'){
        const newState=JSON.parse(JSON.stringify(state));
        newState.datalist=action.list;
        return newState;
    }
    if(action.type==='get_shoucanglist'){
        const newState=JSON.parse(JSON.stringify(state));
        newState.shoucang.push(action.list);
        console.log(newState);
        return newState;
    }
    if(action.type==='del_shoucang'){
        const newState=JSON.parse(JSON.stringify(state));
        newState.shoucang.splice(action.list,1);
        console.log(newState);
        return newState;
    }
    return state;
}