import React from 'react'
import Mytap from './Mytap'
import { withRouter } from 'react-router-dom';
import store from './reduxWork/store'
import  {Flex}  from 'antd-mobile';
const Detail1 = (props) => {
    const id = props.location.pathname.substring(9,12);
   
    console.log(store.getState());
    let datalist = store.getState().datalist;

    
    // console.log(datalist[id-100])
    // let src = datalist[id-100].img;
    let src = datalist[id-100].img;
    let price=datalist[id-100].price;
    let title=datalist[id-100].title;
    
    
    return (
        <div>
            <Flex className='flex'>
                <Flex.Item>
                    <img src={src} style={{width:'360px'}} ></img>
                    <p style={{color:"red"}}>￥{price}</p >
                    <p >{title}</p >
                </Flex.Item>

            </Flex>
            <button style={{
                width:'35px',
                height: '35px',
                borderRadius:'10rem',
                border:0,
                position: 'absolute',
                top:'40px',
                left:'10px',
                display:'block',
                background:'rgba(0, 0, 0, 0.4)',
                color: '#fff'}}
                onClick={props.history.goBack}
            > <i className='iconfont icon-shangyiji'  ></i> </button>
             <Mytap />
        </div>
    )
}

export default withRouter(Detail1)
