import React from 'react'
import Tab from './Tab'
import store from './reduxWork/store'

class Shoucang extends React.Component {
    constructor(){
        super();
        this.state = store.getState();
        this.getList = this.getList.bind(this);
        store.subscribe(this.getList)
    }
    getList(){
        this.setState(store.getState());
    }
    render(){
        const shoucang=store.getState().shoucang;
        const getInfo=(index)=>{
            var arr = shoucang;
            for (var i = index; i < arr.length; i++) {
                return (
                    <div className='flex' >
                            < img style={{ width: '167px', height: '200px',position:'relative' }}  src={arr[i].img} ></img>
                            <p style={{color:"red"}}>￥{arr[i].price}</p >
                            <p>{arr[i].title}</p >
                            <button onClick={()=>{
                                const action = {
                                    type:'del_shoucang',
                                    list:i
                                }
                                store.dispatch(action);
                            }}>取消收藏</button>
                    </div>
                )  
            }
        }
        return (
            <div>
                <div className="flex-container">
                    <div className="sub-title">
                        <button style={{
                        width:'35px',
                        height: '35px',
                        borderRadius:'10rem',
                        border:0,
                        display:'block',
                        background:'rgba(0, 0, 0, 0.4)',
                        color: '#fff'}}
                        onClick={this.props.history.goBack}
                    > <i className='iconfont icon-shangyiji'  ></i> </button>
                        {getInfo(0)}
                        {getInfo(1)}
                        {getInfo(2)}
                        {getInfo(3)}
                        {getInfo(4)}
                        {getInfo(5)}
                        {getInfo(6)}
                        {getInfo(7)}
                        {getInfo(8)}
                        {getInfo(9)}
                    </div>
                </div> 
                <Tab />
            </div>
        )
    }
    
}
export default Shoucang

